import React, { useEffect, useLayoutEffect } from 'react';
import { Link } from 'react-router-dom';
import { useRecoilState } from 'recoil';
import { gnbMenuAtom } from '../atoms';

function Sidebar() {
	const [gnbMenuState, setGnbMenuState] = useRecoilState(gnbMenuAtom);
	const closeGnbMenu = () => {
		setGnbMenuState(false);
	};

	useEffect(() => {
		const sidebarCategorys = document.querySelectorAll('.sidebar-category');
		sidebarCategorys.forEach((el) => {
			el.addEventListener('click', () => {
				sidebarCategorys.forEach((category) => {
					if (category === el) {
						category.classList.toggle('is-active');
						category.classList.toggle('is-open');
					} else {
						category.classList.remove('is-active', 'is-open');
					}
				});
			});
		});
	}, []);

	return (
		<>
			{/* <!-- sidebar --> */}
			<aside className={`sidebar sm-only ${gnbMenuState ? 'is-active' : ''}`}>
				<header className="sidebar-header">
					<h1 className="logo">
						<Link to="/" onClick={closeGnbMenu}>
							<img src="./assets/images/Logo.svg" alt="" />
						</Link>
					</h1>

					<div className="sidebar-user">
						<Link to="/" onClick={closeGnbMenu}>
							<div className="avatar-24">
								<img src="./assets/images/img-user-01.jpg" alt="유저 이미지" />
							</div>
							<strong>사달라</strong>
						</Link>
					</div>
				</header>

				<nav className="sidebar-nav">
					<div className="sidebar-category is-community">
						<header className="sidebar-category-header">
							<button type="button">
								<i className="ic-community"></i>
								<strong>커뮤니티</strong>
								<i className="ic-chevron" aria-hidden></i>
							</button>
						</header>
						<ul className="sidebar-category-list">
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									홈
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									스토어홈
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									카테고리
								</Link>
							</li>
						</ul>
					</div>

					<div className="sidebar-category is-store is-active is-open">
						<header className="sidebar-category-header">
							<button type="button">
								<i className="ic-store"></i>
								<strong>스토어</strong>
								<i className="ic-chevron" aria-hidden></i>
							</button>
						</header>
						<ul className="sidebar-category-list">
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									스토어홈
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									카테고리
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									신혼가구
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									베스트
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									오늘의딜
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									연휴특가
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									월동준비
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									리퍼마켓
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									기획전<i className="ic-new"></i>
								</Link>
							</li>
						</ul>
					</div>

					<div className="sidebar-category is-expert">
						<header className="sidebar-category-header">
							<button type="button">
								<i className="ic-expert"></i>
								<strong>인테리어시공</strong>
								<i className="ic-chevron" aria-hidden></i>
							</button>
						</header>
						<ul className="sidebar-category-list">
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									홈
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									스토어홈
								</Link>
							</li>
							<li className="sidebar-category-item">
								<Link to="/" onClick={closeGnbMenu}>
									카테고리
								</Link>
							</li>
						</ul>
					</div>

					<div className="sidebar-user-menu">
						<ul className="user-menu-list">
							<li className="user-menu-item">
								<Link to="/" onClick={closeGnbMenu}>
									마이페이지
								</Link>
							</li>
							<li className="user-menu-item">
								<Link to="/" onClick={closeGnbMenu}>
									나의 쇼핑
								</Link>
							</li>
							<li className="user-menu-item">
								<Link to="/" onClick={closeGnbMenu}>
									스크랩북
								</Link>
							</li>
							<li className="user-menu-item">
								<Link to="/" onClick={closeGnbMenu}>
									알림
								</Link>
							</li>
							<li className="user-menu-item">
								<Link to="/" onClick={closeGnbMenu}>
									이벤트
								</Link>
							</li>
						</ul>
					</div>
				</nav>
			</aside>
		</>
	);
}

export default Sidebar;
