import React from 'react';
import { Link } from 'react-router-dom';
import { useRecoilState } from 'recoil';
import {
	gnbMenuAtom,
	gnbMyMenuAtom,
	gnbSearchAtom,
	searchHistoryAtom,
} from '../atoms';

function Header() {
	const [gnbMenuState, setGnbMenuState] = useRecoilState(gnbMenuAtom);
	const [gnbSearchState, setGnbSearchState] = useRecoilState(gnbSearchAtom);
	const [gnbMyMenuState, setGnbMyMenuState] = useRecoilState(gnbMyMenuAtom);
	const [isSearchHistory, setIsSearchHistory] =
		useRecoilState(searchHistoryAtom);

	return (
		<>
			{/* <!-- gnb --> */}
			<header className="gnb">
				<div className="container">
					<div className="row">
						<div className="col-sm-4">
							<div className="gnb-wrapper">
								<div className="gnb-left">
									<h1 className="gnb-logo">
										<Link to="/">
											<img
												src="./assets/images/Logo.svg"
												alt="메인페이지로 이동"
											/>
										</Link>
									</h1>

									<nav className="sm-hidden gnb-nav">
										<h2 className="visually-hidden">상세 페이지 이동</h2>
										<ul className="gnb-nav-list">
											<li className="gnb-nav-item">
												<Link to="/productions/220110">커뮤니티</Link>
											</li>
											<li className="gnb-nav-item is-active">
												<Link to="/">스토어</Link>
											</li>
											<li className="gnb-nav-item">
												<Link to="/">인테리어시공</Link>
											</li>
										</ul>
									</nav>

									<button
										onClick={() => setGnbMenuState(true)}
										className="is-menu gnb-icon-button sm-only"
										type="button"
									>
										<i className="ic-menu"></i>
									</button>
								</div>

								<div className="gnb-right">
									<div
										onFocus={() => {
											setIsSearchHistory(true);
										}}
										onBlur={() => {
											setIsSearchHistory(false);
										}}
										className="input-group lg-only"
									>
										<i className="ic-search"></i>
										<input
											className="form-input"
											type="text"
											placeholder="스토어 검색"
											aria-hidden
										/>

										<section
											className={`search-history ${
												isSearchHistory ? 'is-active' : ''
											}`}
										>
											<h1 className="search-history-header">
												<span>최근 검색어</span>
												<button type="button">전체 삭제</button>
											</h1>

											{/* <!-- 최근 검색어가 존재할 때 --> */}
											<div className="search-history-content">
												<ul className="search-history-list">
													<li className="search-history-item">
														<Link to="/">버그</Link>
														<button type="button">
															<i className="ic-close"></i>
														</button>
													</li>
													<li className="search-history-item">
														<Link to="/">튕김버그</Link>
														<button type="button">
															<i className="ic-close"></i>
														</button>
													</li>
													<li className="search-history-item">
														<Link to="/">
															스탠드스탠드스탠드스탠드스탠드스탠드스탠드스탠드스탠드스탠드스탠드
														</Link>
														<button type="button">
															<i className="ic-close"></i>
														</button>
													</li>
												</ul>
											</div>
										</section>
									</div>

									<div className="button-group">
										<button
											onClick={() => setGnbSearchState(true)}
											className="is-search gnb-icon-button lg-hidden"
											type="button"
										>
											<i className="ic-search"></i>
										</button>

										<Link className="gnb-icon-button sm-hidden" to="/">
											<i className="ic-bookmark"></i>
										</Link>

										<Link className="is-bell gnb-icon-button sm-hidden" to="/">
											<i className="ic-bell"></i>
										</Link>

										<Link className="is-cart gnb-icon-button" to="/">
											<i className="ic-cart"></i>
											<strong className="badge">5</strong>
										</Link>

										<div
											onClick={() => {
												setGnbMyMenuState((prev) => !prev);
											}}
											className={`my-menu sm-hidden ${
												gnbMyMenuState ? 'is-active' : ''
											}`}
										>
											<div className="gnb-avatar-button avatar-32 sm-hidden">
												<img
													src="./assets/images/img-review-01.jpg"
													alt="유저 이미지"
												/>
											</div>

											<div className="my-menu-content">
												<ul className="my-menu-list">
													<li className="my-menu-item">
														<Link to="/">마이페이지</Link>
													</li>
													<li className="my-menu-item">
														<Link to="/">나의 쇼핑</Link>
													</li>
													<li className="my-menu-item">
														<Link to="/">이벤트</Link>
													</li>
													<li className="my-menu-item">
														<Link to="/">로그아웃</Link>
													</li>
												</ul>
											</div>
										</div>
									</div>

									{/* <!-- 로그인 하지 않았을 때 -->
                  <!-- <div className="button-group">
                    <Link className="is-cart gnb-icon-button" to="/">
                      <i className="ic-cart"></i>
                    </Link>

                    <div className="sm-hidden gnb-auth">
                      <Link to="/">로그인</Link>
                      <Link to="/">회원가입</Link>
                    </div>
                  </div> --> */}

									<button
										className="btn-40 btn-primary sm-hidden"
										type="button"
									>
										글쓰기
										<i className="ic-chevron"></i>
									</button>
								</div>
							</div>
						</div>
					</div>
				</div>
			</header>

			<nav className="lnb">
				<div className="container">
					<div className="row">
						<div className="col-sm-4">
							<ul className="lnb-list">
								<li className="lnb-item is-active">
									<Link to="/">스토어</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">홈카테고리</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">신혼가구</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">베스트</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">오늘의딜</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">연휴특가</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">월동준비</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">리퍼마켓</Link>
								</li>
								<li className="lnb-item">
									<Link to="/">
										기획전<i className="ic-new"></i>
									</Link>
								</li>
							</ul>
						</div>
					</div>
				</div>
			</nav>
		</>
	);
}

export default Header;
